body {
  background-color: #696969;
}

button {
  color: #000;
  cursor: pointer;
  background-color: #f0f8ff;
  border: 1px;
  border-radius: 4px;
  padding: 10px 25px;
  font-size: 18px;
  font-weight: bold;
  box-shadow: 0 4px 4px rgba(0, 0, 0, .15);
}

.header {
  background-color: #1e90ff;
  justify-content: center;
  padding-top: 20px;
  padding-bottom: 20px;
  display: flex;
}

.input {
  justify-content: center;
  align-items: center;
  padding: 10px 20px;
  font-size: 20px;
}

.gallery {
  grid-template-columns: repeat(4, 1fr);
  gap: 20px;
  display: grid;
}

.photo-card {
  background-color: #d3d3d3;
  border: 1px solid #789;
  border-radius: 4px;
  overflow: hidden;
}

.photo-card img {
  width: 100%;
  height: 500px;
  object-fit: cover;
}

.info {
  justify-content: center;
  gap: 25px;
  font-size: 20px;
  display: flex;
}

.load-more-container {
  justify-content: center;
  display: flex;
}

.load-more {
  margin-top: 20px;
  display: none;
}

